module.exports = "/* Importar fuentes custom*/\r\n@font-face{\r\n\tfont-family: \"yanone\";\r\n\tsrc: url('yanone.ttf');\r\n}\r\n/*Generales*/\r\n*{\r\n\tmargin: 0px;\r\n\tpadding: 0px;\r\n}\r\nbody{\r\n\tfont-family: \"yanone\", Arial, Helvetica;\r\n\tfont-size: 20px;\r\n\tbackground: url(\"data:image/png;base64,\");\r\n\tbackground-attachment: fixed;\r\n}\r\n.clearfix{\r\n\tfloat:none;\r\n\tclear:both;\r\n}\r\n/*Barra de navegacion / cabecera*/\r\nheader{\r\n\tdisplay: block;\r\n\twidth: 100%;\r\n\theight: 90px;\r\n\tbackground: rgba(53, 103, 164, 0.8);\r\n\tposition: fixed;\r\n\ttext-transform: uppercase;\r\n}\r\nheader ul{\r\n\tdisplay: block;\r\n\twidth: 40%;\r\n\theight: inherit;\r\n\tmargin: 0px auto;\r\n\tmargin-top: -3px;\r\n}\r\nheader ul li{\r\n\tdisplay: inline-block;\r\n\tmargin: 0px auto;\r\n\ttext-align: center;\r\n\tline-height: 88px;\r\n}\r\nheader a{\r\n\tdisplay: block;\r\n\tpadding-right: 20px;\r\n\tpadding-left: 20px;\r\n\theight: inherit;\r\n\ttext-decoration: none;\r\n\tcolor:white;\r\n\ttransition: all 300ms;\r\n}\r\nheader a:hover,\r\n.actived{\r\n\tbackground: rgba(0,0,0,0.8);\r\n}\r\n/*Logotipo*/\r\n#logo{\r\n\tfont-family: \"yanone\";\r\n\tfont-size: 30px;\r\n}\r\n#logo a{\r\n\ttransition: color 500ms, -webkit-transform 500ms;\r\n\ttransition: color 500ms, transform 500ms;\r\n\ttransition: color 500ms, transform 500ms, -webkit-transform 500ms;\r\n}\r\n#logo a:hover{\r\n\tbackground: transparent;\r\n\tcolor: black;\r\n\t-webkit-transform:scale(1.50,1.50);\r\n\t        transform:scale(1.50,1.50);\r\n}\r\n/*Contenedor principal*/\r\n#content{\r\n\tmin-height: 1000px;\r\n\theight: auto;\r\n\twidth: 75%;\r\n\tmargin: 0px auto;\r\n\tbox-shadow: 0px 0px 40px black;\r\n\tborder-left:1px solid #b7b7b7;\r\n\tborder-right:1px solid #b7b7b7;\r\n\tbackground: rgba(255, 255, 255, 0.7);\r\n\tpadding-top: 120px;\r\n}\r\n/*Pie de pagina*/\r\nfooter{\r\n\twidth: 75%;\r\n\tmargin: 0px auto;\r\n\tpadding-top: 15px;\r\n\tcolor: rgba(0, 0, 0, 0.5);\r\n}\r\n/*Sobre mi*/\r\n#title{\r\n\twidth: 70%;\r\n\tmargin: 0px auto;\r\n\ttext-align: center;\r\n\tmargin-top: 20px;\r\n\tmargin-bottom: 70px;\r\n}\r\n#title h1{\r\n\tfont-family: \"yanone\";\r\n\tfont-size: 60px;\r\n\tborder-bottom: 1px solid #ccc;\r\n\tpadding-bottom: 5px;\r\n}\r\n#title h2{\r\n\tfont-size: 25px;\r\n\tcolor: gray;\r\n\tmargin-top: 5px;\r\n\tmargin-bottom: 5px;\r\n}\r\narticle.about{\r\n\twidth: 70%;\r\n\tmargin: 0px auto;\r\n\tmargin-bottom: 20px;\r\n\tfont-size: 23px;\r\n}\r\n.about h2{\r\n\tborder-bottom: 1px solid #ccc;\r\n\tpadding-bottom: 5px;\r\n\tmargin-bottom: 5px;\r\n}\r\n.about p{\r\n\tpadding-top: 20px;\r\n\tpadding-bottom: 30px;\r\n\ttext-align: justify;\r\n}\r\n/*Sección con formulario*/\r\n.container{\r\n\tpadding: 50px;\r\n\tpadding-top: 10px;\r\n}\r\n.container .image{\r\n\tfloat: left;\r\n\twidth: 47%;\r\n}\r\n.container .image img{\r\n\twidth: 100%;\r\n}\r\n.container .data{\r\n\tfloat: left;\r\n\twidth: 48%;\r\n\tmargin-left:30px;\r\n}\r\n.container h2{\r\n\tdisplay: block;\r\n\tborder-bottom: 1px solid #ccc;\r\n\tpadding-bottom: 10px;\r\n\tmargin-bottom: 15px;\r\n}\r\nform{\r\n\twidth: 80%;\r\n}\r\nform label{\r\n\tdisplay: block;\r\n\twidth: 100%;\r\n\tmargin-top: 10px;\r\n\tmargin-bottom: 5px;\r\n}\r\nform input[type=\"text\"],\r\nform input[type=\"number\"],\r\nform input[type=\"email\"],\r\nform textarea{\r\n\twidth: 40%;\r\n\tpadding: 5px;\r\n}\r\nform button,\r\nform input[type=\"submit\"],\r\n.button-edit,\r\n.button-delete{\r\n\tdisplay: block;\r\n\tfont-size: 17px;\r\n\tpadding: 10px;\r\n\tmargin-top: 15px; \r\n\tcolor:white;\r\n\tbackground: rgba(53, 103, 164, 1);\r\n\tborder: 1px solid rgba(13, 67, 133, 1);\r\n\tcursor: pointer;\r\n}\r\n.button-edit,\r\n.button-delete{\r\n\twidth: 20%;\r\n\tfloat: left;\r\n\tmargin-right: 10px;\r\n\ttext-decoration: none;\r\n}\r\n.button-edit{\r\n\tbackground: orange;\r\n\tborder: none;\r\n}\r\n.button-delete{\r\n\tbackground: red;\r\n\tborder: none;\r\n}\r\nform button:hover,\r\nform input[type=\"submit\"]:hover{\r\n\tbackground: rgba(53, 103, 164, 0.8);\r\n\tborder: 1px solid rgba(13, 67, 133, 1);\r\n}\r\nform input[disabled]{\r\n\topacity: 0.4;\r\n\tcursor: not-allowed;\r\n}\r\n.form_error{\r\n\tfont-size: 13px;\r\n\tpadding: 3px;\r\n\tbackground: red;\r\n\tcolor:white;\r\n\tmargin-left: 5px;\r\n}\r\n.message{\r\n\twidth: 50%;\r\n\tpadding: 5px;\r\n\tborder: 1px solid #eee;\r\n}\r\n.success{\r\n\tbackground: green;\r\n\tcolor: white;\r\n}\r\n.message a{\r\n\tcolor: white;\r\n}\r\n.failed{\r\n\tbackground: red;\r\n\tcolor: white;\r\n}\r\n/*Pagina de proyectos*/\r\n.project{\r\n\tlist-style: none;\r\n\tdisplay: block;\r\n\tfloat: left;\r\n\twidth: 28%;\r\n\ttext-align: center;\r\n\tmargin-left: 20px;\r\n\tmargin-right: 20px;\r\n\tmargin-bottom: 30px;\r\n}\r\n.project .image{\r\n\twidth: 100%;\r\n\theight: 150px;\r\n\toverflow: hidden;\r\n}\r\n.project img{\r\n\twidth: 100%;\r\n}\r\n.project a{\r\n\ttext-decoration: none;\r\n\tcolor: black;\r\n}"